.startPage{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 80%;
    max-width: 1220px;
    max-height: 830px;
    height: 80%;
    transform-origin: left top;
    background-color: $base-background-color;
    border: 4px rgba(29,29,27,.15) solid;
    border-radius: 2%;
    box-shadow: inset 0px 2px 0px 0px rgba(255,255,255,.15),0px 3px 0px 0px rgba(255,255,255,0.15);

    &--hide{
       animation: hide 1s ease-in-out forwards;
    }

    @keyframes hide{
        0%{
            transform: scale(1) translate(-50%, -50%);
         }

        100%{
           transform: scale(0) translate(-50%, -50%);
        }
    }

    .startPage__logo{
        display: flex;
        justify-content: center;
        align-items: center;
        height: 30%;
    }

    .startPage__logo-image{
        height: 80%;
    }

    .startPage__lang{
        cursor: pointer;
        position: absolute;
        top: 5%;
        left: 5%;
        display: flex;
        align-items: center;
        padding: 5px 10px;
        color: $primary-color;
        width: 9%;
        border-radius: 8px;
        background-color: rgba(38, 28, 92, 0.3);
        border: 2px solid rgba(255, 255, 255, 0.6);
        transition: .2s;

        &:hover{
            background-color: lighten(rgba(38, 28, 92, 0.3), 60%);
        }
    }

    .lang__globe{
        margin-right: 14%;
        font-size: 2rem;

        @media (max-width: 1300px){
            font-size: 1.5rem;
        }
        
        @media (max-width: 1100px){
            font-size: 1.2rem;
        }
    }

    .lang__p{
        font-size: 1.5rem;
        user-select: none;

        @media (max-width: 1300px){
            font-size: 1.3rem;
        }

        @media (max-width: 1100px){
            font-size: 1.1rem;
        }
    }
}

.startPage__main{
    width: 90%;
    height: 58%;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;

    .startPage__play{
        flex-basis: 60%;
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        
        
        .startPage__chose {
            display: block;
            height: 12%;
            width:calc(50% - 3px);
            text-transform: uppercase;
            -webkit-text-stroke: 1px black;
            letter-spacing: 1px;    
            font-size: 1.1rem;
            font-weight: bold;
            color: $primary-color;
            background-color: rgba(165, 64, 5, 0.924);
            border: none;
            border-radius: 12px 12px 0 0;  
            transition: .2s;
            cursor: pointer;

            @media (max-width: 1100px){
                font-size: 1rem;
            } 

            @media (max-width: 950px){
                font-size: 0.8rem;
            }
        }

        .button--unactive{
            &:hover{
                background-color: lighten(rgba(165, 64, 5, 0.924), 2%);
            }
           }

        .button--active{
            color: $contrast-color;
            font-size: 1.6rem;

            @media (max-width: 1400px){
                font-size: 1.3rem;
            }

            @media (max-width: 1100px){
                font-size: 1.1rem;
            } 

            @media (max-width: 950px){
                font-size: 0.9rem;
            }
        }

        .space{
            height: 6px;
            background-color: rgba(165, 64, 5, 0.924);
            width: calc(50% - 3px);

            &--second{
                margin-left:  calc(50% + 3px);
            }
        }
    }
}

.play{
    width: 100%;
    height: 87%;
    border-radius: 0 0 12px 12px;
    background-color: rgba(165, 64, 5, 0.924);
    display: flex;
    flex-wrap: wrap;

    .play__avatar{
        position: relative;
        flex-basis: 40%;
        height: 65%;
    }
    

    .play__nickname{
        flex-basis: 60%;
        height: 65%;
        position: relative;

        .nickname__p{
            position: absolute;
            top: 40%;
            left: 50%;
            transform: translateX(-50%);
            width: 70%;
            text-align: center;
            text-transform: uppercase;
            color: $primary-color;
            letter-spacing: 1px;
            font-size: 1.5rem;

            @media (max-height: 800px){
                top: 25%;
            }

            @media (max-width: 1000px){
                font-size: 1.4rem;
            }
            @media (max-width: 900px){
                font-size: 1.2rem;
            }
        }

        .nickname--error{
            animation: nickname .2s linear alternate-reverse ;
        }

        @keyframes nickname {
            50%{
                transform: translateX(-55%);
            }
            100%{
                transform: translateX(-45%);
            }
        }

        .nickname__input{
            position: absolute;
            bottom: 7%;
            left: 50%;
            transform: translateX(-50%);
            width: 70%;
            padding: 3%;
            border-radius: 12px;
            color: $primary-color;
            background-color: rgba(255,255,255,.3);
            border: 2px rgba(255,255,255,.7) solid;
            font-size: 1.2rem;

            &::placeholder{
                color:  rgba(214, 214, 214, 0.322);
            }

            &:-ms-input-placeholder{
                color:  rgba(214, 214, 214, 0.322);
            } 
            &::-ms-input-placeholder{
                color:  rgba(214, 214, 214, 0.322);
            }

            &:focus{
                outline:none;
            }
        }

    }

    .play__start-wraper{
        flex-basis: 100%;
        height: 35%;
        position: relative;
    }
}

.multi{
    width: 100%;
    height: 87%;
    border-radius: 0 0 12px 12px;
    background-color: rgba(165, 64, 5, 0.924);
    display: flex;
    justify-content: center;
    align-items: center;

    .multi__apologise{
        border: 2px solid $primary-color;
        padding: 1rem;
        border-radius: 7px;
        display: block;
        width: 60%;
        text-align: center;
        font-size: 1.4rem;
        line-height: 2rem;
        color: $primary-color;
        font-family: $digital-font;
    }
}

.avatar__container{
    position: absolute;
    top: 60%;
    left: 60%;
    transform: translate(-50%, -50%);
    width: 75%;
    border-radius: 50%;
    z-index: 2;

    .avatar__refresh{
        position: absolute;
        width: 20%;
        left: 50%;
        bottom: 10px;
        cursor: pointer;
        transition: .2s;
        transform: translate(-50%, 50%) rotate(-50deg);

        &:hover{
            transform: translate(-50%, 50%);    
        }
    }

    .avatar__img{
        width: 100%;
        border: 4px solid $primary-color;
        border-radius: 50%;
        user-select: none;
    }
}

.play__button{
    display: flex;
    align-items: center;
    justify-content: space-between  ;
    cursor: pointer;    
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: #eee;
    border: none;
    border-radius: 14px;
    font-size: 1.5rem;
    color: rgba(124, 150, 138, 0.961);
    font-weight: bold;
    text-transform: uppercase;
    width: 30%;
    height: 40%;
    padding: 0 10px;
    box-shadow: 0px 6px 0px 0px rgb(110, 31, 0);
    transition: .2s;

    @media(max-width: 1200px){
        font-size: 1.3rem;
    }

    @media(max-width: 900px){
        font-size: 1rem;
    }


    &:hover{
        background-color: darken($primary-color, 10%);
    }

    p{
        flex-grow: 1;
        text-align: center;
    }

    .play__button-icon{
        height: 1.8rem;
       margin-left: 5%;

       @media(max-width: 1100px){
        font-size: 1.2rem;
    }
    }
}

.howToPlay{
    position: relative;
    flex-basis: 35%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.034);
    border: 4px solid rgba(0, 0, 0, 0.075);
    border-radius: 12px;

    .howToPlay__icon-container{
        width: 100%;
        padding-top: 20px;
        height: 40%;
        display: flex;
        justify-content: center;
        align-items: center;

        @media(max-height: 750px){
            padding-top: 45px;
            height: 50%;
        }
    }

    .howToPlay__text{
        width: 80%;
        margin: 0 auto;
    }

    .text__h2{
        font-size: 1.5rem;
        text-align: center;
        color: $primary-color;
        font-weight: normal;

        @media(max-width: 1150px){
            font-size: 1.2rem;
        }
    }

    .text__mainContent{
        text-align: center;
        font-weight: bold;
        color: $primary-color;
        margin-top: 5%;
        font-family: $digital-font;
        font-size: 1.4rem;

        @media(max-width: 1150px){
            font-size: 1.2rem;
        }

        @media(max-height: 940px){
            font-size: 1.2rem;
        }

        @media(max-height: 750px){
            display: none;
        }
    }

    .howToPlay__icon{
        font-size: 4.5rem;
        color: $primary-color;
    }

    .howToPlay__header{
        display: block;
        margin-top: 3%;
        -webkit-text-stroke: 2px black;
        color: $contrast-color;
        text-align: center;
        text-transform: uppercase;
        font-size: 1.6rem;

        @media (max-width: 1400px){
            font-size: 1.4rem;
        }

        @media (max-width: 1100px){
            font-size: 1.2rem;
        } 

        @media (max-width: 950px){
            font-size: 1.1rem;
        }
    }

    .howToPlay__main{
        width: 100%;
        height: 80%;
    }
    .howToPlay__navigation{
        width: 80%; 
        margin: 0 auto;
    }
    .navigation__list{
        width: 100%;
        list-style: none;
        display: flex;
        justify-content: space-around;
    }

    .list__element{
        cursor: pointer;
        position: relative;
        width: 18px;
        height: 18px;
        background-color: $primary-color;
        border-radius: 50%;
        transition: .2s;

        &--active{
            transform: scale(1.2);
            background-color: $contrast-color;      
          }
    }
    
}

.howToPlay__bar{
    position: absolute;
    width: 30px;
    height: 30px;
    top: 2%;
    left: 3%;

    @media (max-width: 1000px){
        width: 20px;
        height: 20px;
    }

    .CircularProgressbar-path {
        stroke: rgb(255, 255, 255);
      }
      .CircularProgressbar-trail {
        stroke: transparent;
      }
}

.startPage__footer{
    width: 100%;
    height: 12%;
    position: relative;

    .footer__info{
        position: absolute;
        top:50%;
        left: 50%;
        transform: translate(-50%,-50%);
        font-size: 1rem;
        font-weight: bold;
        text-transform: uppercase;
        color: $primary-color;

        a{
            font-family: Arial, "Helvetica Neue", Helvetica, sans-serif;
            text-decoration: none;
            color: $primary-color;
        }
    }

    .footer__gitlogo{
        position: absolute;
        left: 5%;
        bottom: 50%;
        transform: translateY(50%);
        height: 2rem;
    }
}



@media screen and (max-width: $large) and (max-aspect-ratio: 13/9) {
    .startPage{
        width: 90%;
        height: 100%;
        max-height: 100%;
        border: none;
        box-shadow: none;
        background: none;
        display: flex;
        flex-direction: column;
        justify-content: space-between;

        .startPage__lang{
            top: 2%;
            left: 1%;
           width: auto;


           .lang__p{
            font-size: .9rem;
           }
        }

        .startPage__logo{
            padding-top: 2%;
            height: 20%;

            .startPage__logo-image{
                height:60%;
            }
        }

        .startPage__main{
            justify-content: center;
            margin-top: 10%;
        }

        .startPage__play{
            flex-basis: 100%;
            max-width: 320px;
            align-items: flex-start;
            height: 70%;

            .startPage__chose{
                font-size: 1rem;
                @media (max-width: 350px){
                    font-size: 0.8rem;
                }

                @media screen and (max-height: 612px) {
                    font-size: .8rem;
                }
            }    
        }

        .startPage__info{
            position: absolute;
            top: 2.2%;
            right: 1%;
            color: $primary-color;
            font-size: 1.6rem;
        }

        .footer__info{
            top:0;
          width: 80%;
          text-align: center;
        }

        .multi__apologise{
            width: 80%;
            font-size: 1rem;
        }

        .play{
            height: 87%;
            display: flex;
            flex-direction: column;
            flex-wrap: nowrap;
        }

        .play__button{
            width: 50%;

            @media screen and (max-height: 612px) {
                margin-top: 5%;
                height: 70%;
            }

            .play__button-icon{
               height: 1.2rem;
            }
        }

        .play__avatar{
           flex-basis: 55%;
           @media screen and (max-height: 612px) {
            flex-basis: 30%;
        }
            
            .avatar__container{
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);
                width: 35%;

                @media screen and (max-height: 612px) {
                    display: none;
                }

                .avatar__img{
                    width: 100%;
                    border: 3px solid rgb(219, 219, 219);
                }

                .avatar__refresh{
                    z-index: 2;
                }
            }
        }

        .play__nickname{
            flex-basis: 20%;

            .nickname__p{
                display: none;
            }

            .nickname__input{
                padding: 5px 10px;
                bottom: 50%;
                transform: translate(-50%, 50%);
            }
        }

        .play__start-wraper{
            flex-basis: 25%;
        }
    }

    .howToPlay{
        position: absolute;
        top: 0;
        left: 0;
        display: block;
        transform: translate(-5%, 0);
        border-radius: 0;
        height: 100vh;
        width: 100vw;
        border: none;
        background-color: rgba(0, 0, 0, 0.719);
        z-index: 4;

        .howToPlay__header{
            font-size: 1.4rem;
        }

        .howToPlay__main{
            display: flex;
            flex-direction: column;
            justify-content: space-around;
        }

        .howToPlay__text{
            height: 40%;
        }

        .howToPlay__close{
            cursor: pointer;
            position: absolute;
            top: 2%;
            right: 7%;
            background-color: transparent;
            border: none;
            font-size: 1.6rem;
            color: $primary-color;
        }

        .text__mainContent{
            display: block;
        }
    }
}

@media screen and (max-width: $large) and  (min-aspect-ratio: 13/9){
    .startPage{
        display: none;
    }
}